@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: "Montserrat", sans-serif;
}
:root {
  --main-color: rgb(0, 255, 171);
  --dimmed-background-black: rgba(0, 0, 0, 0.6);
  --dimmed-background-black-1: rgba(0, 0, 0, 0.8);
  --main-box-shadow: 2px 2px 5px black;
  --main-border-radius: 15px;
}
body {
  position: relative;
  margin: 2rem 0;
  padding: 0;
  width: 100%;
  height: auto;
  background: var(--main-color);
  background: radial-gradient(
    circle,
    rgba(0, 255, 171, 1) 0%,
    rgba(0, 101, 91, 1) 100%
  );
  /* overflow: hidden; */
}
h2 {
  color: var(--main-color);
  font-weight: 700;
  text-align: center;
}
